import Api from '@/common/api';
import { formatDate } from '@/common/date';
export default {
	components:{

	},
	data() {
		return {

		}
	},
	props:{
		item:{
			type: Object
		},
		types:{
			type: String,
			default: ''
		},
		theme:{
			type: String,
			default: '#ff9000'
		},
		solid:{
			type: String,
			default: '#ffffff'
		},
		color:{
			type: String,
			default: '#ff9000'
		},
	},
	methods: {
        async acceptCoupon(item){
		       uni.showLoading({
        				title: '请稍后'
        			});

        			let params = { couponId: item.id };
        			let data = await Api.apiCall('post', Api.marking.acceptCoupon, params);
			this.$api.msg(data.msg);
        			if (data) {
        				this.$api.msg(data.msg);
        			}
        			uni.hideLoading();

		},
		dateFormat(time) {
			if (time == null || time === '') {
				return 'N/A';
			}
			let date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
		}
	}
}
